<template>
  <v-container>
    <v-row>
      <v-list dense>
        <v-list-item
          v-for="(task, index) in tasks"
          :key="'task' + index"
        >
          <v-sheet
            color="grey"
            dark
            width="28"
            height="28"
            class="mx-auto text-center pt-1"
          >
            {{ index+1 }}</v-sheet
          >

          <v-list-item-content>
            <v-subheader>{{ task.label }}</v-subheader>
          </v-list-item-content>

          <!-- <v-list-item-icon>
            <v-icon color="green">mdi-check-bold</v-icon>
          </v-list-item-icon> -->
        </v-list-item>
      </v-list>
    </v-row>
  </v-container>
</template>

<script>
export default {
  name: "Progress",
  data() {
    return {
      tasks: [
        { label: "Create HTML design", done: true },
        { label: "Correct HTML design", done: false },
        { label: "Go Live", done: false },
      ],
    };
  },
};
</script>
